// helper.js

import config from "../../../../config";

const BASE_URL = `${config.API_URL}/customerLead`;

const callApi = async (endpoint, method = "GET", data = null) => {
  const url = `${BASE_URL}${endpoint}`;
  const options = {
    method,
    headers: {
      "Content-Type": "application/json",
    },
  };

  if (data) {
    options.body = JSON.stringify(data);
  }

  try {
    const response = await fetch(url, options);
    const result = await response.json();
    return result;
  } catch (error) {
    console.error("API Error:", error.message);
    throw new Error("Failed to fetch data from the server");
  }
};

// Get all customers
const getAllApi = async () => {
  return callApi("/");
};

// Create a new customer
const createApi = async (request) => {
  return callApi("/", "POST", request);
};

// Update an existing customer
const editApi = async (id, request) => {
  return callApi(`/${id}`, "PUT", request);
};

// Delete a customer
const deleteApi = async (id) => {
  return callApi(`/${id}`, "DELETE");
};

export { getAllApi, createApi, editApi, deleteApi };
