import config from "../../../../config";

// userHelper.js
const BASE_URL = `${config.API_URL}/user`;

const callApi = async (endpoint, method = "GET", data = null) => {
  const url = `${BASE_URL}${endpoint}`;
  const options = {
    method,
    headers: {
      "Content-Type": "application/json",
    },
  };

  if (data) {
    options.body = JSON.stringify(data);
  }

  try {
    const response = await fetch(url, options);
    const result = await response.json();
    return result;
  } catch (error) {
    console.error("API Error:", error.message);
    throw new Error("Failed to fetch data from the server");
  }
};

const getAllUsers = async () => callApi("/");
const createUser = async (user) => callApi("/", "POST", user);

const editUser = async (userId, userData) => {
  // Exclude password from the request if not provided
  const { password, ...rest } = userData;
  const updatedUserData = {
    ...rest,
  };

  // If updating password, include it in the request
  if (password) {
    updatedUserData.password = password;
  }

  return callApi(`/${userId}`, "PATCH", updatedUserData);
};

const deleteUser = async (userId) => callApi(`/${userId}`, "DELETE");

export { getAllUsers, createUser, editUser, deleteUser };
