// AddEditUser.jsx
import React from "react";
import { Modal, Button, Col, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { FormInput } from "../../../../components";
// import { createUser, editUser } from "./userHelper";

const schemaResolver = yupResolver(
  yup.object().shape({
    username: yup.string().required("Please enter username"),
    firstName: yup.string().required("Please enter first name"),
    lastName: yup.string().notRequired(),
    role: yup.string().required("Please enter role"),
  })
);

const AddEditUser = ({ isView, user, show, onHide, onSubmit }) => {
  const title = user?._id ? (isView ? "View" : "Edit") : "Add";

  const methods = useForm({ resolver: schemaResolver });
  const {
    handleSubmit,
    register,
    control,
    formState: { errors },
  } = methods;

  const handleFormSubmit = (formData) => {
    onSubmit(formData);
  };

  return (
    <Modal
      show={show}
      onHide={onHide}
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header className="bg-light" onHide={onHide} closeButton>
        <Modal.Title className="m-0">{`${title} User`}</Modal.Title>
      </Modal.Header>
      <Modal.Body className="p-4">
        <form onSubmit={handleSubmit(handleFormSubmit)}>
          <Row>
            <Col xs={6}>
              <FormInput
                label="Username"
                type="text"
                name="username"
                defaultValue={user?.username}
                disabled={isView}
                register={register}
                errors={errors}
                control={control}
              />
              <FormInput
                label="First Name"
                type="text"
                name="firstName"
                defaultValue={user?.firstName}
                disabled={isView}
                register={register}
                errors={errors}
                control={control}
              />
              <FormInput
                label="Last Name"
                type="text"
                name="lastName"
                defaultValue={user?.lastName}
                disabled={isView}
                register={register}
                errors={errors}
                control={control}
              />
              {!isView && (
                <FormInput
                  label="Password"
                  type="password"
                  name="password"
                  placeholder="Enter password"
                  containerClass={"mb-3"}
                  register={register}
                  errors={errors}
                  control={control}
                />
              )}
              <FormInput
                label="Role"
                type="text"
                name="role"
                defaultValue={user?.role}
                disabled={isView}
                register={register}
                errors={errors}
                control={control}
              />
            </Col>
          </Row>
          <div className="text-end">
            <Button
              variant="success"
              type="submit"
              className="waves-effect waves-light me-1"
              disabled={isView}
            >
              Save
            </Button>
            <Button
              variant="danger"
              className="waves-effect waves-light"
              onClick={onHide}
            >
              Continue
            </Button>
          </div>
        </form>
      </Modal.Body>
    </Modal>
  );
};

export default AddEditUser;
