import React from "react";
import { Modal, Button, Col, Row } from "react-bootstrap";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { FormInput } from "../../../../components";
import { useForm } from "react-hook-form";

const AddEditCustomerLead = ({
  isView = false,
  data,
  show,
  onHide,
  onSubmit,
  assinees,
}) => {
  /*
    form validation schema
    */
  const schemaResolver = yupResolver(
    yup.object().shape({
      name: yup.string().required("Please enter name"),
      email: yup
        .string()
        .required("Please enter email")
        .email("Please enter a valid email"),
      phone: yup
        .string()
        .required("Please enter phone")
        .matches(/^\d{10}$/, "Phone number is not valid"),
      company: yup.string().required("Please enter company"),
      category: yup.string().required("Please enter category"),
      source: yup.string().required("Please enter source"),
      assignee: yup.string().notRequired(),
      createdDate: yup
        .date()
        .default(() => new Date())
        .notRequired(),
      leadStatus: yup.string().default("Open").notRequired(),
    })
  );

  const viewOrEdit = isView ? "View" : "Edit";
  const title = data?.id ? viewOrEdit : "Add";

  const methods = useForm({ resolver: schemaResolver });
  const {
    handleSubmit,
    register,
    control,
    formState: { errors },
  } = methods;

  const userString = sessionStorage.getItem("ubold_user") || "";
  const user = JSON.parse(userString);

  return (
    <>
      <Modal
        show={show}
        onHide={onHide}
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header className="bg-light" onHide={onHide} closeButton>
          <Modal.Title className="m-0">{title} Customer Lead</Modal.Title>
        </Modal.Header>
        <Modal.Body className="p-4">
          <form
            id="addEditCustomerLeads"
            name="addEditCustomerLeads"
            onSubmit={handleSubmit(onSubmit)}
          >
            <Row>
              <Col xs={6}>
                <FormInput
                  label="Full Name"
                  type="text"
                  name="name"
                  placeholder="Enter full name"
                  disabled={isView}
                  defaultValue={data?.name}
                  containerClass={"mb-3"}
                  className="form-control"
                  register={register}
                  errors={errors}
                  control={control}
                />
                <FormInput
                  label="Email address"
                  type="email"
                  name="email"
                  disabled={isView}
                  defaultValue={data?.email}
                  placeholder="Enter email"
                  containerClass={"mb-3"}
                  register={register}
                  errors={errors}
                  control={control}
                />
                <FormInput
                  label="Phone"
                  type="text"
                  name="phone"
                  disabled={isView}
                  defaultValue={data?.phone}
                  placeholder="Enter phone number"
                  containerClass={"mb-3"}
                  register={register}
                  errors={errors}
                  control={control}
                />
                <FormInput
                  label="Company"
                  type="text"
                  name="company"
                  disabled={isView}
                  defaultValue={data?.company}
                  placeholder="Enter company"
                  containerClass={"mb-3"}
                  register={register}
                  errors={errors}
                  control={control}
                />
              </Col>
              <Col xs={6}>
                <FormInput
                  label="Category"
                  type="text"
                  name="category"
                  disabled={isView}
                  defaultValue={data?.category}
                  placeholder="Enter category"
                  containerClass={"mb-3"}
                  register={register}
                  errors={errors}
                  control={control}
                />
                <FormInput
                  label="Source"
                  type="text"
                  name="source"
                  disabled={isView}
                  defaultValue={data?.source}
                  placeholder="Enter source"
                  containerClass={"mb-3"}
                  register={register}
                  errors={errors}
                  control={control}
                />
                {user?.role === "Admin" && (
                  <FormInput
                    label="Assignee"
                    type="select"
                    name="assignee"
                    disabled={isView || user?.role !== "Admin"}
                    defaultValue={data?.assignee}
                    placeholder="Enter assignee"
                    containerClass={"mb-3"}
                    register={register}
                    errors={errors}
                    control={control}
                  >
                    {assinees?.map((x) => (
                      <option value={x}>{x}</option>
                    ))}
                  </FormInput>
                )}
                <FormInput
                  label="Status"
                  type="select"
                  name="leadStatus"
                  disabled={isView}
                  defaultValue={data?.status}
                  placeholder="Enter Status"
                  containerClass={"mb-3"}
                  register={register}
                  errors={errors}
                  control={control}
                >
                  <option value="Ongoing">Ongoing</option>
                  <option value="Closed">Closed</option>
                  <option value="Rejected">Rejected</option>
                </FormInput>
              </Col>
            </Row>

            <div className="text-end">
              <Button
                variant="success"
                type="submit"
                className="waves-effect waves-light me-1"
                disabled={isView}
              >
                Save
              </Button>
              <Button
                variant="danger"
                className="waves-effect waves-light"
                onClick={onHide}
              >
                Continue
              </Button>
            </div>
          </form>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default AddEditCustomerLead;
