import React, { useEffect, useState } from "react";
import PageTitle from "../../../../components/PageTitle";
import { Row, Col, Card, Button } from "react-bootstrap";
import classNames from "classnames";
import { Link } from "react-router-dom";
import Table from "../../../../components/Table";
import AddEditCustomerLead from "./AddEditCustomerLeads";
import { getAllApi, createApi, deleteApi, editApi } from "./helper";
import { FormInput } from "../../../../components";
import { getSalesUsers } from "../common";

const AssignUser = (props) => {
  const onChange = (e) => {
    props.onAssigneeChange({
      ...props?.row?.original,
      assignee: e?.target?.value,
    });
  };
  return (
    <FormInput
      placeholder="Select Assignee"
      onChange={onChange}
      type="select"
      value={props.row?.original?.assignee}
    >
      {props.options?.map((x) => (
        <option value={x}>{x}</option>
      ))}
    </FormInput>
  );
};

const StatusColumn = ({ row }) => {
  return (
    <React.Fragment>
      <span
        className={classNames("badge", {
          "bg-soft-success text-success": row.original.leadStatus === "Ongoing",
          "bg-soft-danger text-danger": row.original.leadStatus === "Closed",
          "bg-soft-danger text-primary": row.original.leadStatus === "Rejected",
        })}
      >
        {row.original.leadStatus}
      </span>
    </React.Fragment>
  );
};

const ActionColumn = ({ row, onViewClick, onEditClick, onDeleteClick }) => {
  const handleView = () => {
    onViewClick(row?.original);
  };
  const handleEdit = () => {
    onEditClick(row?.original);
  };
  const handleDelete = () => {
    onDeleteClick(row?.original);
  };
  return (
    <>
      <Link to="#" className="action-icon" onClick={handleView}>
        {" "}
        <i className="mdi mdi-eye"></i>
      </Link>
      <Link to="#" className="action-icon" onClick={handleEdit}>
        {" "}
        <i className="mdi mdi-square-edit-outline"></i>
      </Link>
      <Link to="#" className="action-icon" onClick={handleDelete}>
        {" "}
        <i className="mdi mdi-delete"></i>
      </Link>
    </>
  );
};

export default function CustomerLeads() {
  const [customerLeads, setCustomerLeads] = useState([]);
  const [selectedData, setSelectedData] = useState({});
  const [salesUsers, setSalesUsers] = useState([]);

  useEffect(() => {
    loadCustomerLeads();
    getSalesUsers().then((response) => setSalesUsers(response));
  }, []);

  const loadCustomerLeads = () => getAllApi().then(setCustomerLeads);
  const [isView, setIsView] = useState(false);
  const [openModal, setOpenModal] = useState(false);
  const onOpenModal = () => setOpenModal(true);
  const onCloseModal = () => {
    setOpenModal(false);
    loadCustomerLeads();
  };

  const onAssigneeChange = (updatedRow) => {
    editApi(updatedRow?.id, updatedRow).then(() => {
      loadCustomerLeads();
    });
  };

  const assignees = ["Unassigned"].concat(salesUsers?.map((x) => x?.username));

  const userString = sessionStorage.getItem("ubold_user") || "";
  const user = JSON.parse(userString);

  const columns = [
    {
      Header: "Customer",
      accessor: "name",
      classes: "table-user",
    },
    {
      Header: "Phone",
      accessor: "phone",
      sort: false,
    },
    {
      Header: "Email",
      accessor: "email",
      sort: false,
    },
    {
      Header: "Company",
      accessor: "company",
      sort: false,
    },
    {
      Header: "Category",
      accessor: "category",
      sort: false,
    },
    {
      Header: "Source",
      accessor: "source",
      sort: false,
    },
    {
      Header: "Assignee",
      accessor: "assignee",
      sort: false,
      Cell: (cellProps) => (
        <AssignUser
          {...cellProps}
          options={assignees}
          onAssigneeChange={onAssigneeChange}
        />
      ),
      role: "Admin",
    },
    {
      Header: "Create Date",
      accessor: "createdDate",
      sort: false,
    },
    {
      Header: "Status",
      accessor: "leadStatus",
      sort: false,
      Cell: StatusColumn,
    },
    {
      Header: "Action",
      accessor: "action",
      sort: false,
      classes: "table-action",
      Cell: (cellProps) => (
        <ActionColumn
          {...cellProps}
          onViewClick={onViewClick}
          onEditClick={onEditClick}
          onDeleteClick={onDeleteClick}
        />
      ),
    },
  ].filter((x) => x.accessor !== "assignee" || user?.role === "Admin");

  const onSubmit = (data) => {
    if (user?.role !== "Admin") {
      data.assignee = user?.username;
    }
    if (selectedData?.id) {
      editApi(selectedData?.id, data).then(() => {
        onCloseModal();
        setSelectedData({});
        setIsView(false);
        loadCustomerLeads();
      });
    } else {
      createApi(data).then(() => {
        onCloseModal();
        setSelectedData({});
        setIsView(false);
        loadCustomerLeads();
      });
    }
  };

  const onViewClick = (row) => {
    setOpenModal(true);
    setSelectedData(row);
    setIsView(true);
  };

  const onEditClick = (row) => {
    setOpenModal(true);
    setSelectedData(row);
    setIsView(false);
  };

  const onDeleteClick = (row) => {
    deleteApi(row?.id).then(() => loadCustomerLeads());
  };

  const [mode, setMode] = useState("All");
  const onAllClick = () => setMode("All");
  const onClosedClick = () => setMode("Closed");
  let data =
    mode === "All"
      ? customerLeads
      : customerLeads?.filter((x) => x.leadStatus === "Closed");

  data =
    user?.role === "Admin"
      ? data
      : data?.filter((x) => x.assignee === user?.username);

  return (
    <>
      <PageTitle
        breadCrumbItems={[
          { label: "CRM", path: "/apps/crm/customerleads" },
          {
            label: "Customer Leads",
            path: "/apps/crm/customerleads",
            active: true,
          },
        ]}
        title={"Customer Leads"}
      />
      <Row>
        <Col>
          <Card>
            <Card.Body>
              <Row className="mb-2">
                <Col sm={4}>
                  <Button
                    variant="danger"
                    className="waves-effect waves-light"
                    onClick={onOpenModal}
                  >
                    <i className="mdi mdi-plus-circle me-1"></i> Add Customer
                    Leads
                  </Button>
                  &nbsp;
                  <Button disabled={mode === "All"} onClick={onAllClick}>
                    All
                  </Button>
                  &nbsp;
                  <Button disabled={mode === "Closed"} onClick={onClosedClick}>
                    Closed
                  </Button>
                </Col>
                <Col></Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Table
                    columns={columns}
                    data={data?.map((x) => ({
                      ...x,
                      createdDate: x.createdDate?.split("T")?.[0],
                    }))}
                    pageSize={12}
                    isSortable={true}
                    pagination={true}
                    tableClass="table-nowrap table-striped"
                  />
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      {openModal && (
        <AddEditCustomerLead
          show={openModal}
          onHide={onCloseModal}
          isView={isView}
          data={selectedData}
          onSubmit={onSubmit}
          assinees={assignees}
        />
      )}
    </>
  );
}
