// Users.jsx
import React, { useEffect, useState } from "react";
import PageTitle from "../../../../components/PageTitle";
import { Row, Col, Card, Button } from "react-bootstrap";
import Table from "../../../../components/Table";
import AddEditUser from "./AddEditUser";
import { getAllUsers, createUser, editUser, deleteUser } from "./userHelper";

const Users = () => {
  const [users, setUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState({});
  const [openModal, setOpenModal] = useState(false);
  const [isView, setIsView] = useState(false);

  useEffect(() => {
    loadUsers();
  }, []);

  const loadUsers = () => getAllUsers().then(setUsers);

  const onOpenModal = () => {
    setOpenModal(true);
    setIsView(false);
    setSelectedUser({}); // Reset selected user when opening the modal
  };

  const onCloseModal = () => {
    setOpenModal(false);
    loadUsers();
  };

  const onSubmitUser = (userData) => {
    if (selectedUser?._id) {
      editUser(selectedUser?._id, userData).then(() => {
        onCloseModal();
        setSelectedUser({});
        loadUsers();
      });
    } else {
      createUser(userData).then(() => {
        onCloseModal();
        setSelectedUser({});
        loadUsers();
      });
    }
  };

  const onViewUserClick = (user) => {
    setOpenModal(true);
    setSelectedUser(user);
    setIsView(true);
  };

  const onEditUserClick = (user) => {
    setOpenModal(true);
    setSelectedUser(user);
    setIsView(false);
  };

  const onDeleteUserClick = (userId) => {
    deleteUser(userId).then(() => loadUsers());
  };

  const userString = sessionStorage.getItem("ubold_user") || "";
  const user = JSON.parse(userString);

  const data =
    user.role === "Admin"
      ? users
      : users?.filter((x) => x.username === user.username);

  const columns = [
    {
      Header: "Username",
      accessor: "username",
    },
    {
      Header: "First Name",
      accessor: "firstName",
    },
    {
      Header: "Last Name",
      accessor: "lastName",
    },
    {
      Header: "Role",
      accessor: "role",
    },
    {
      Header: "Actions",
      accessor: "actions",
      Cell: ({ row }) => (
        <>
          <Button variant="link" onClick={() => onViewUserClick(row.original)}>
            <i className="mdi mdi-eye"></i> View
          </Button>
          <Button variant="link" onClick={() => onEditUserClick(row.original)}>
            <i className="mdi mdi-square-edit-outline"></i> Edit
          </Button>
          <>
            {row?.original?.role !== "Admin" && (
              <Button
                variant="link"
                onClick={() => onDeleteUserClick(row.original._id)}
              >
                <i className="mdi mdi-delete"></i> Delete
              </Button>
            )}
          </>
        </>
      ),
    },
  ]?.filter((x) => {
    if (user?.role === "Admin") {
      return true;
    } else {
      if (x.accessor === "actions") {
        return false;
      }
    }
    return true;
  });

  return (
    <>
      <PageTitle
        breadCrumbItems={[
          { label: "Users", path: "/users" },
          {
            label: data?.role !== "Admin" ? "Your Info" : "All Users",
            path: "/users",
            active: true,
          },
        ]}
        title={data?.role !== "Admin" ? "Your Info" : "All Users"}
      />
      <Row>
        <Col>
          <Card>
            <Card.Body>
              {user.role === "Admin" && (
                <Row className="mb-2">
                  <Col sm={4}>
                    <Button
                      variant="danger"
                      className="waves-effect waves-light"
                      onClick={onOpenModal}
                    >
                      <i className="mdi mdi-plus-circle me-1"></i> Add User
                    </Button>
                  </Col>
                </Row>
              )}
              <Row>
                <Col sm={12}>
                  <Table
                    columns={columns}
                    data={data}
                    pageSize={12}
                    isSortable={true}
                    pagination={true}
                    tableClass="table-nowrap table-striped"
                  />
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      {openModal && (
        <AddEditUser
          show={openModal}
          onHide={onCloseModal}
          isView={isView}
          user={selectedUser}
          onSubmit={onSubmitUser}
        />
      )}
    </>
  );
};

export default Users;
